<template>
    <div>
    <div class="my-5 row justify-content-center">
    <form class="col-md-6" @submit.prevent="payChecked">
        <table class="table">
        <thead>
            <th>品名</th>
            <th>數量</th>
            <th>單價</th>
        </thead>
        <tbody>
            <tr v-for="item in order.products" :key="item.id">
            <td class="align-middle">{{ item.product.title }}</td>
            <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
            <td class="align-middle text-right">{{ item.final_total | currency }}</td>
            </tr>
        </tbody>
        <tfoot>
            <tr>
            <td colspan="2" class="text-right">總計(已使用優惠價格)</td>
            <td class="text-right">{{ order.total+60 | currency }}</td>
            </tr>
        </tfoot>
        </table>

        <table class="table">
        <tbody>
            <tr>
            <th width="100">Email</th>
            <td>{{ order.user.email }}</td>
            </tr>
            <tr>
            <th>姓名</th>
            <td>{{ order.user.name }}</td>
            </tr>
            <tr>
            <th>收件人電話</th>
            <td>{{ order.user.tel }}</td>
            </tr>
            <tr>
            <th>收件人地址</th>
            <td>{{ order.user.address }}</td>
            </tr>
            <tr>
            <th>付款狀態</th>
            <td>
                <span v-if="!order.is_paid">尚未付款</span>
                <span v-else class="text-success">付款完成</span>
            </td>
            </tr>
        </tbody>
        </table>
        <div class="text-right" v-if="order.is_paid === false">
        <button class="btn btn-danger">確認付款去</button>
        </div>
    </form>
    </div>
    <div class="row justify-content-center"><button class="btn btn-success" @click="backHome">返回首頁</button></div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            orderId:'',
            order:{
                user:{}
            }
        }
    },
    methods:{
        getOrder(){
            let vm = this
			let url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order/${vm.orderId}`
            this.axios.get(url).then((res)=>{
                console.log('getOrder',res)
                vm.order = res.data.order
            })
        },
        payChecked(){
                this.isLoading = true
                let vm = this
                let url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/pay/${vm.orderId}`
                this.axios.post(url).then((res)=>{
                    console.log('payChecked',res)
                    vm.getOrder()
                    vm.isLoading=false
            })
        },
        backHome(){
            this.$router.push('/')
        }
    },
    created(){
        this.orderId = this.$route.params.orderId
        this.getOrder()
    }
}
</script>



